@import '../helpers/helpers';

.account{
    &-page{
        padding: 30px 0;
    }
    &-section{
        display: flex;
        justify-content: space-between;
        margin: 0 auto 50px;
        @include breakpoint(md) {
           flex-direction: column;
        }
    }
    &-content{
        width: 100%;
        padding: 0 50px;
        @include breakpoint(md) {
            padding: 0 10px;
        }
        .tab-content {
            display: none;
            &.active {
                display: block;
            }
        }
        .tab-title{
            @include font-style('Poppins', 600, 54px, 81px);
            text-transform: uppercase;
            margin-bottom: 40px;
            @include breakpoint(xxl) {
                font-size: 36px;
                line-height: 60px;
            }
            @include breakpoint(md) {
                font-size: 30px;
                line-height: 54px;
            }
            @include breakpoint(sm) {
                display: none;
            }
        }
        .subtitle{
            margin: 20px 0;
            @include breakpoint(sm) {
                font-size: 20px;
                line-height: 30px;
            }
        }
        .desc{
            @include breakpoint(sm) {
                font-size: 14px;
                line-height: 16px;
            }
        }
    }
    &-nav{
        width: 100%;
        max-width: 300px;
        margin: 120px 0 100px;
        width: 100%;
        flex-shrink: 0;
        @include breakpoint(lg) {
            max-width: 220px;
        }
        @include breakpoint(md) {
            max-width: fit-content;
            overflow-x: auto;
            margin: 20px 0;
            display: flex;
        }
        @include breakpoint(sm) {
            margin: 20px -15px 0;
            width: 100vw;
        }
        &::-webkit-scrollbar {
            width: 0  !important;
        }
        
        &::-webkit-scrollbar-track {
            background: transparent  !important;
            border-radius: 0 !important;
        }
        
        &::-webkit-scrollbar-thumb {
            background: transparent  !important;
            border-radius: 0  !important;
        }
        ul{
            border-right: 5px solid rgba(217, 217, 217, 0.2);
            position: relative;
            @include breakpoint(md) {
                display: flex;
                border: 0;
                flex-shrink: 0;
            }
            li{
                flex-shrink: 0;
                @include breakpoint(md) {
                    margin: 0 10px;
                    display: block;
                }
               a{
                    @include font-style('Poppins', 600, 22px, 36px);
                    width: 100%;
                    padding: 10px 0;
                    display: block;
                    position: relative;
                    outline: none;
                    opacity: 1;
                    @include breakpoint(xxl) {
                        font-size: 18px;
                        line-height: 27px;
                        
                    }
                    @include breakpoint(md) {
                        font-size: 14px;
                        line-height: 21px;
                        border-bottom: 2px solid transparent;
                    }
                    &.active{
                        @include breakpoint(md) {
                            border-bottom: 2px solid var(--color_bright_primary);
                        }
                        &:after{
                            background: var(--color_bright_primary);
                        }
                    }
                    &:after{
                        @include transition;
                        content: '';
                        top: 0;
                        right: -5px;
                        width: 5px;
                        height: 100%;
                        position: absolute;
                        background: transparent;
                        @include breakpoint(md) {
                            display: none;
                        }
                    }
                    &:not(.active){
                        color: rgba(51, 51, 51, 0.6);
                    }
               }
            }
        }
    }
    &-logout{
        display: flex;
        align-items: center;
        margin-top: 100px;
        @include font-style('Poppins', 600, 22px, 36px);
        @include breakpoint(xxl) {
            font-size: 18px;
            line-height: 27px;
            
        }
        @include breakpoint(md) {
            font-size: 14px;
            line-height: 21px;
            margin: 0 20px;
            flex-shrink: 0;
        }
        svg{
            margin-right: 10px;
            flex-shrink: 0;
            @include breakpoint(md) {
                max-width: 20px;
            }
        }
    }
}

#coupons{
    ul{
        max-width: 300px;
        width: 100%;
        li{
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 1px solid rgba(51, 51, 51, 0.2);
            padding: 10px 0;
            &:not(.used){
                span{
                    user-select: all;
                }
            }
            &.used{
                user-select: none;
                span{
                    text-decoration: line-through;
                    color: rgba(51, 51, 51, 0.5);
                }
            }
        }
    }
}

#details{
    form{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        fieldset{
            width: 48%;
            @include breakpoint(lg) {
                width: 100%;
            }
        }
        .btn-col{
            width: 100%;
            .btn-primary{
                margin-left: 0;
                width: 48%;
                @include breakpoint(xl) {
                    width: 100%;
                }
            }
        }
    }
}

.order{
    .products-mini {
        max-width: 100%;
        .info{
            width: 100%;
        }
    }
    &_item{
        position: relative;
        &:not(:first-child){
            .order_title{
                border-top: 1px solid rgba(51,51,51,.2)
            }
        }
        &:last-child{
            .order_title{
                border-bottom: 1px solid rgba(51,51,51,.2)
            }
            &.opened{
                .order_title{
                    border-bottom: 1px solid transparent;
                } 
            }
        }
        &.opened{
            svg.open{
                transform: rotate(180deg);
            }
            .order_content_cover{
                height: var(--el-height);
            }
            + .order_item{
                .order_title{
                    border-top: 1px solid transparent;
                }
            }
            .order_title{
                &:after{
                    opacity: 0;
                }
            }
        }

    }
    .heading-status{
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    .heading{
        p{
            &:nth-child(1){
                @include font-style('Poppins', 600, 20px, 30px);
                @include breakpoint(xl) {
                    font-size: 16px;
                    line-height: 20px;
                }
                @include breakpoint(md) {
                    font-size: 14px;
                    line-height: 16px;
                }
            }
            &:nth-child(2){
                @include font-style('Rubik', 600, 16px, 19px);
                color: rgba(51, 51, 51, 0.8);
                margin: 10px 0;
                @include breakpoint(xl) {
                    font-size: 14px;
                    line-height: 17px;
                }
                @include breakpoint(md) {
                    font-size: 14px;
                    line-height: 16px;
                }
            }
            &:nth-child(3){
                @include font-style('Roboto', 300, 16px, 19px);
                color: rgba(51, 51, 51, 0.8);
                @include breakpoint(xl) {
                    font-size: 14px;
                    line-height: 17px;
                }
                @include breakpoint(md) {
                    font-size: 14px;
                    line-height: 16px;
                }
            }
        }
    }
    svg.open{
        @include transition();
        max-width: 30px;
    }
    .date{
        @include font-style('Poppins', 300, 14px, 16px);
        color: rgba(51, 51, 51, 0.8);
    }
    .status{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-right: 20px;
        p{
            >svg{
                max-width: 25px;
                margin-right: 5px;
            }
        }
        svg{
            margin-right: 10px;
        }
        .progress{
            margin-top: 5px;
            margin-left: auto;
            left: 5px;
            position: relative;
        }
        &.inprogress{
            color: rgba(0, 133, 255, 1);
        }
        &.canceled{
            color: rgba(219, 3, 3, 1);
        }
        &.delivered{
            color:rgba(0, 224, 79, 1);
        }
    }
    .products-list{
        margin-top: 20px;
    }
    .total{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 30px 0 0;
        padding: 20px 0;
        border-top: 1px solid rgba(51,51,51,.2);
        span{
            &:first-child{
                @include font-style('Poppins', 600, 18px, 27px);
            }
            &:last-child{
                @include font-style('Poppins', 600, 24px, 28px);
            }
        }
    }
    .order-details{
        .subtitle-label{
            margin: 20px 0;
            display: block;
        }
        .block{
            p{
                padding-bottom: var(--spacing-2);
            }
            p, span{
                display: flex;
                justify-content: space-between;
                align-items: center;
                svg{
                    margin-right: 10px;
                }
            }
            p span{
                &:first-child{
                    @include font-style('Poppins', 300, 20px, 30px);
                    color: rgba(51, 51, 51, 0.4);
                    @include breakpoint(xxl) {
                        font-size: 16px;
                        line-height: 24px;
                    }
                    @include breakpoint(md) {
                        font-size: 14px;
                        line-height: 21px;
                    }
                }
                &:last-child{
                    @include font-style('Poppins', 600, 20px, 30px);
                    text-align: right;
                    margin-left: 20px;
                    @include breakpoint(xxl) {
                        font-size: 16px;
                        line-height: 24px;
                    }
                    @include breakpoint(md) {
                        font-size: 14px;
                        line-height: 21px;
                    }
                }
            }
        }
    }
    &_title{
        position: relative;
        @include transition();
        display: flex;
        justify-content: space-between;
        padding: 20px;
        align-items: flex-start;
        border-bottom: 1px solid rgba(51,51,51,.2);
        &:after{
            @include transition();
            content: '';
            position: absolute;
            height: 75%;
            width: 5px;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background: #00e04f;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
        }
    }
    &_content_cover{
        @include transition();
        transition-timing-function: ease-in-out;
        margin: 0 20px 0;
        overflow: hidden;
    }
    &_content{
        display: block;
    }
}

.orders-empty{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 40px;
    @include breakpoint(md) {
        padding: 20px;
    }
    svg{
        margin-bottom: 20px;
    }
    .btn-primary{
        margin-top: 50px;
        @include breakpoint(md) {
            padding-left: 0;
            padding-right: 0;
            width: 100%;
            max-width: 100%;
        }
    }
    .subtitle{
        @include font-style('Poppins', 600, 32px, 48px);
        text-align: center;
        @include breakpoint(xl) {
            font-size: 20px;
            line-height: 30px;  
        }
    }
    .desc{
        @include font-style('Roboto', 300, 20px, 23px);
        @include breakpoint(xl) {
            font-size: 16px;
            line-height: 19px;
        }
        @include breakpoint(md) {
            font-size: 14px;
            line-height: 21px;
            text-align: center;
        }
    }
}

.photo-block{
    display: flex;
    align-items: center;
    margin: 20px 0 40px;
    >p{
        @include font-style('Poppins', 400, 32px, 48px);
        margin-left: 10px;
        @include breakpoint(xxl) {
            font-size: 24px;
            line-height: 36px; 
        }
        b{
            display: block;
        }
    }
    .upload-container {
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: rgba(51, 51, 51, 1);
        @include breakpoint(lg) {
            width: 70px;
            height: 70px;
        }
    }
    
    #photoInput {
        display: none;
    }
    
    .upload-label {
        display: block;
        height: 100%;
        cursor: pointer;
    }
    
    #previewImage {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        color: rgba(51, 51, 51, 1);
        border-radius: 100%
    }
    
    .upload-text {
        @include font-style('Poppins', 600, 55px, 108px);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: rgba(255, 255, 255, 1);
        @include breakpoint(lg) {
            font-size: 40px;
            line-height: 80px;
        }
    }
    svg{
        position: absolute;
        right: 0;
        bottom: 0;
        @include breakpoint(lg) {
            max-width: 30px;
        }
    }
}

#address{
    form{
        margin: 20px 0;
        fieldset{
            @include breakpoint(lg) {
                flex-direction: column;
            }
            p{
                width: 48%;
                &:last-child{
                    @include breakpoint(lg) {
                        margin-top: 20px;
                    }
                }
                @include breakpoint(lg) {
                    width: 100%;
                }
            }
        }
        .btn-primary{
            margin-left: 0;
            width: 48%;
            @include breakpoint(xl) {
                width: 100%;
            }
        }
    }
    .desc{
        @include font-style('Roboto', 300, 20px, 23px);
        display: flex;
        align-items: center;
        margin: 20px 0;
        @include breakpoint(md) {
            font-size: 14px;
            line-height: 18px;
        }
        svg{
            margin-right: 10px;
        }
    }
    .extra{
        @include font-style('Roboto', 400, 16px, 19px);
        background: rgba(159, 159, 159, 0.3);
        padding: 10px;
        margin: 10px 0;
        @include breakpoint(md) {
            font-size: 16px;
            line-height: 19px;
        }
    }
}

input[type="checkbox"] {
    accent-color: rgba(51, 51, 51, 1);
    width: 25px;
    height: 25px;
}
.wishlist-products{
    padding: 40px 20px;
    width: 100%;
    overflow: auto;
    .headings{
        display: grid;
        grid-template-columns: 50% 15% 35%;
        padding-bottom: 20px;
        border-bottom:1px solid rgba(51, 51, 51, 0.2);
        @include font-style('Poppins', 600, 18px, 27px);
        @include breakpoint(xxll) {
            grid-template-columns:54% 15% 18%;
        }
        @include breakpoint(xl) {
            width: fit-content;
            grid-template-columns: 510px 147px 340px;
        }
    }
    .list{
        @include breakpoint(xl) {
            width: fit-content;
        }
        li{
            display: grid;
            grid-template-columns:50px 200px 35% 15% 20% 12% 1%;
            border-bottom:1px solid rgba(51, 51, 51, 0.2);
            align-items: center;
            @include breakpoint(xxl) {
                grid-template-columns:50px 200px 33% 15% 20% 12% 1%;
            }
            @include breakpoint(xxll) {
                grid-template-columns:50px 200px 32% 16% 13% 12% 1%;
            }
            @include breakpoint(xl) {
                grid-template-columns:50px 145px 320px 145px 175px 145px 20px;
                padding: 10px 0;
            }
            img{
                max-width: 150px;
                @include breakpoint(md) {
                    max-width: 110px;
                }
            }
            .info{
                .product-serie{
                    @include font-style('Roboto', 300, 18px, 24px);
                    padding: 20px 0;
                    color: rgba(51, 51, 51, 1);
                    @include breakpoint(md) {
                        padding: 10px 0;
                    }
                }
                .product-title{
                    @include font-style('Roboto', 500, 20px, 23px);
                    width: 90%;
                    display: block;
                }
                .star-rating{
                    @include font-style('Roboto', 300, 18px, 24px);
                }
            }
            .remove{
                cursor: pointer;
            }
            .price{
                @include font-style('Rubik', 400, 24px, 21px);
            }
            .btn-primary{
                padding: 10px 20px;
                p{
                    @include font-style('Poppins', 400, 14px, 21px);
                    text-transform: initial;
                }
            }
            .status{
                @include font-style('Roboto', 600, 18px, 21px);
                &.outoftock{
                    color: rgba(142, 30, 30, 0.6);
                }
                &.instock{
                    color: rgba(31, 131, 54, 0.6);
                }
            }
        }
    }
}
#wishlist{
    .wishlist-products{
        .headings{
            grid-template-columns: 52% 15% 23%;
            @include breakpoint(xxl-extra) {
                grid-template-columns: 54% 15% 17%;
            }
            @include breakpoint(xxl) {
                width: fit-content;
                grid-template-columns: 510px 147px 340px;
            }
        }
        .list{
            li{
                grid-template-columns:50px 200px 32% 16% 13% 12% 1%;
                @include breakpoint(xxl) {
                    grid-template-columns:50px 145px 320px 145px 175px 145px 20px;
                    padding: 10px 0;
                }
                img{
                    max-width: 110px;
                }
                .info{
                    .product-serie{
                        padding: 10px 0;
                    }
                }
            }
        }
    }
}

#wishlist, .wishlist-page{
    .wrapper{
        margin: 20px 0;
        .actions-row{
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin: 20px 0;
            @include breakpoint(sm) {
                flex-direction: column;
            }
            p{
                @include transition;
                display: flex;
                align-items: center;
                color: rgba(51, 51, 51, 0.5);
                text-decoration: underline;
                &:hover{
                    opacity: 0.5;
                }
                svg{
                    margin-left: 10px;
                }
            }
        }
        .nice-select{
            border-radius:var(--radius_3);
            min-width: 300px;
            margin-right: 20px;
            @include breakpoint(lg) {
                min-width: 230px;
                margin-bottom: 20px;
            }
            @include breakpoint(sm) {
                width: 100%;
            }
            &.open .list{
                min-width: 300px;
                @include breakpoint(lg) {
                    min-width: 230px;
                }
                @include breakpoint(sm) {
                    width: 100%;
                }
            }
        }
        .btn-primary{
            --lineheight: 21px;
            @include breakpoint(sm) {
                width: 100%;
            }
        }
        .add-all{
            margin: 30px 0;
            margin-left: auto;
            @include breakpoint(sm) {
                width: 100%;
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}